.ub-button-content-holder .ub-button-image {
	align-self: center;
}

.ub-button-wipe {
	&-left:hover:after,
	&-right:hover:after,
	&-up:hover:after,
	&-down:hover:after {
		width: 100%;
		height: 100%;
	}

	&-left:after {
		top: 0;
		right: 0;
		width: 0;
		height: 100%;
	}
	&-right:after {
		top: 0;
		left: 0;
		width: 0;
		height: 100%;
	}
	&-up:after {
		bottom: 0;
		right: 0;
		width: 100%;
		height: 0;
	}
	&-down:after {
		top: 0;
		left: 0;
		width: 100%;
		height: 0;
	}
}
